@import "../../abstracts/variables.scss";

.listitem {
  display: flex;
  position: relative;
  border-radius: 15px;
  border: 0px solid transparent;
  flex-direction: column;
  background-color: $color-white;

  &__button {
    display: flex;
    border-radius: 15px;
    width: 100%;
    padding: 16px;
    background-color: $color-white;
    gap: 20px;
    // z-index: 2;

    &:hover {
      color: $color-black-op04;
    }
  }

  &__name {
  }

  &__number {
    opacity: 0.4;
  }

  &_active {
    background-color: $color-red;
    color: $color-white;
  }
}

.taskitem {
  opacity: 1;
  &_active {
    opacity: 1;
  }
}
